import React, {Component} from "react";
import storage from "./Config";
import {MDBCol, MDBContainer, MDBRow} from "mdbreact";
import "./protfolio.css";
import Check from "./check";

class Admin extends Component {
    constructor(props) {
        super(props);
        this.state = {
            image: null,
            url: [],

            progress: 0,
            photoIndex: 0,
            isOpen: false
        };
    }
    handleUpload = () => {
        let imageUrl = [];
        const {image} = this.state;
        const uploadTask = storage.ref(`images/${this.state.blobs.name}`).put(this.state.blobs);
        uploadTask.on(
            "state_changed",
            snapshot => {
                // progress function ...
                const progress = Math.round(
                    (snapshot.bytesTransferred / snapshot.totalBytes) * 100
                );
                this.setState({progress});
            },
            error => {
                // Error function ...
                console.log(error);
            },
            () => {
                // complete function ...
                storage
                    .ref("images")
                    .child(this.state.blobs.name)
                    .getDownloadURL()
                    .then(url => {
                        var local = this.state.url
                        local.push(url);
                        this.setState({url: local});
                    });
                console.log(this.state.url);
            }
        );
    };

   blob=(e)=>{
        console.log(e);
        this.setState({blobs: e})
    }







    render() {
        const {photoIndex, isOpen} = this.state;

        return (

            <div className="center">
                <br/>
                <h2 className="green-text">React Firebase Image Uploader</h2>
                <br/>
                <br/>

                <br/>
                <br/>
                <br/>
                <div className="file-field input-field">
                    <div className="btn">
                        <span>File</span>
                        <Check blob={this.blob}/>
                    </div>
                    <div className="file-path-wrapper">
                        <progress value={this.state.progress} max="100" className="progress"/>
                    </div>
                </div>
                <button

                    onClick={this.handleUpload}
                    className="waves-effect waves-light btn btn-dark"
                >
                    Upload
                </button>
                <br/>
                <br/>
                <MDBContainer>
                    <div className="mdb-lightbox no-margin">
                        <MDBRow>

                            {this.state.url.map((item, index) => (
                                <MDBCol md="4">
                                    <figure>
                                        <img
                                            src={this.state.url[index]}
                                            alt="Gallery"
                                            className="img-fluid"
                                            style={{
                                                width:'100%',
                                                height:'auto'/2,
                                            }}
                                            onClick={() =>

                                                this.setState({photoIndex: 0, isOpen: true})
                                            }
                                        />
                                    </figure>
                                </MDBCol>
                            ))}


                        </MDBRow>
                    </div>
                </MDBContainer>
            </div>
        );
    }
}

export default Admin;